.modal {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    display: none;

    z-index: 2;
}

.modalBackground {
    display: none;

    background-color: rgba(0, 0, 0, .4);

    z-index: 1;
}

.modal.show {
    display: block;
}

.modalBackground.show {
    position: absolute;
    top: 0;
    left: 0;

    width: -webkit-fill-available;
    height: -webkit-fill-available;

    display: block;
}

.modalTitle {
    position: absolute;
    top: 50%;
    left: 50%;

    width: 400px;
    height: 600px;

    padding: 40px;

    text-align: center;

    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    box-shadow: 0 2px 3px 0 rgba(34, 36, 38, 0.15);

    transform: translateX(-50%) translateY(-50%);
}

.modalDescription {
    position: absolute;
    top: 55%;
    left: 50%;

    width: 400px;
    height: 600px;

    text-align: center;

    transform: translateX(-50%) translateY(-50%);
}

.skillImages {
    margin-top: 1%;
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }
    to {
        opacity: 1;
        transform: translateZ(0);
    }
}